  position: relative;
}
.timer-widget .timer-slider .timer-range::before {
  background-image: url(https://static.iviva.com/images/Udhayimages/sun.png);
  content: "";
  width: 40px;
  height: 40px;
  display: block;
  background-size: 100% auto;
  position: absolute;
  top: -18px;
  left: -50px;
