  background-position: center center;
}
.timer-widget .timer-slider .timer-range::after {
  background-image: url(https://static.iviva.com/images/Udhayimages/moon.png);
  content: "";
  width: 35px;
  height: 35px;
  display: block;
  background-size: 100% auto;
  position: absolute;
  bottom: -20px;
  right: -45px;
