  font-size: 0.9rem;
}
.timer-widget .timer-slider .tooltip:before {
  content: "";
  position: absolute;
  border-style: solid;
  border-width: 8px 0 8px 8px;
  border-color: transparent transparent transparent #023d28;
  top: calc(50% - 8px);
  right: -8px;
}
